* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #f5f4fa;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

button {
  color: #fff;
  letter-spacing: .06em;
  cursor: pointer;
  background-color: #2196f3;
  border: #2196f3;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px 42px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  text-decoration: none;
  transition-property: background-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 4px #00000026;
}

button:hover {
  color: #2196f3;
  background-color: #fff;
}

#datetime-picker {
  border-color: #2196f3;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 25px;
}

.timer {
  letter-spacing: .06em;
  gap: 5px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  display: flex;
}

.field {
  border: 2px solid #2196f3;
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  display: flex;
}

.value {
  letter-spacing: .06em;
  font-size: 25px;
  font-weight: 700;
  line-height: 1.875;
}

.label {
  color: gray;
  letter-spacing: .06em;
  font-size: 16px;
  line-height: 1.875;
}

.form {
  letter-spacing: .06em;
  flex-direction: column;
  align-items: end;
  gap: 5px;
  font-size: 16px;
  line-height: 1.875;
  display: flex;
}

